import React, { Component } from "react"
import { Input } from "../components/Input"
import { Operations } from "../components/Operations"
import { Output } from "../components/Output"
import { Title } from "../components/Title"

export class Greet extends Component{
    constructor(){
        super();
        console.log("in constructor");
        this.inputs={
            'first':"",
            'last':"",
        }
        this.message='';
        this.state={'message':this.message};
    }
    properCase(str){
        return str.charAt(0).toUpperCase() + str.substr(1).toLowerCase();
    }
    printWelcome(){
        let firstName=this.properCase(this.inputs['first']);
        let lastName=this.properCase(this.inputs['last']);
        this.message=`Welcome ${firstName} ${lastName}`;
        this.setState({message:this.message});
        console.log(this.message);
    }
    takeInput(event,key){
        console.log(event.target.value);
        this.inputs[key]=event.target.value;
        console.log("in TakeInput ");
    }
    render(){
        console.log("in render");
        return(
               <div className='container'>
                    <Title/>
                    <Input fnKey='first' name='First Name' fn={this.takeInput.bind(this)}/>
                    <Input fnKey='last' name='Last Name' fn={this.takeInput.bind(this)}/>                        
                    <br/>
                    <Operations fn={this.printWelcome.bind(this)} name='Greet' myClass='btn-primary' /> &nbsp;
                    <Operations name='Clear All' myClass='btn-danger'/>
                   <Output msg={this.message}/>  
                </div>
              );
    }
}

